import React from "react"
import axios from "axios";
import { Link } from "gatsby"
 
import { useForm, usePlugin, useCMS  } from 'tinacms'
 
import SEO from "../components/seo"
import Layout from "../components/layout"
import { rhythm, scale } from "../utils/typography"
 
import ReactMarkdown from "react-markdown"
 
const ArticlePost = ({pageContext, location}) => {
    const { slug, next, previous } = pageContext;
 
    const cms = useCMS();
 
    const loadInitialValues = async () => {
      const entry = await axios.get(`http://localhost:5000/article/${slug}`);
      return entry.data;
    }
  
    const formConfig = {
      id: slug,
      label: 'Edit Article',
      fields: [
        {
          name: 'title',
          label: 'Title',
          component: 'text',
        },
        {
          name: 'summary',
          label: 'summary',
          component: 'text',
        },
        {
          name: 'description',
          label: 'description',
          component: 'text',
        },
        {
          name: 'description',
          label: 'description',
          component: 'markdown',
        },
      ],
      loadInitialValues,
      onSubmit: async (formData) => {
        const payload = {
          ...formData,
          slug: slug
        }
  
        const response = await axios.put(`http://localhost:5000/article/${payload.slug}`, payload)
  
        if (response.status === 200){
          cms.alerts.success('Saved!')
        }else{
          cms.alerts.error('failed!')
        }
      },
    }
  
    const [modifiedValues, form] = useForm(formConfig, { fields: formConfig.fields })
  
    usePlugin(form)
 
    return (
    <Layout location={location} title={modifiedValues?.title}>
        <SEO title={`Post ${modifiedValues?.title}`} />
        <article itemScope itemType="http://schema.org/Article">
        <header>
          <h1
            itemProp="headline"
            style={{
              marginTop: rhythm(1),
              marginBottom: 0,
            }}
          >
            {modifiedValues.title}
          </h1>
          <p
            style={{
              ...scale(-1 / 5),
              display: `block`,
              marginBottom: rhythm(1),
            }}
          >
            {modifiedValues.summary}
          </p>
        </header>
        
        <ReactMarkdown source={modifiedValues.description}/>
 
        <hr
          style={{
            marginBottom: rhythm(1),
          }}
        />
      </article>
 
      <nav>
        <ul
          style={{
            display: `flex`,
            flexWrap: `wrap`,
            justifyContent: `space-between`,
            listStyle: `none`,
            padding: 0,
          }}
        >
          <li>
            {previous && <Link to={`/${previous.slug}/`}>
            ← {previous.title}
            </Link>}
          </li>
          <li><Link to="/">Home</Link></li>
          <li>
            {next && (
              <Link to={`/${next.slug}/`}>
                {next.title} →
              </Link>
            )}
          </li>
        </ul>
      </nav>
 
    </Layout>);
}
 
export default ArticlePost;